import React from "react";
import logo from "./logo.jpeg";
import IT from "./IT.jpeg";
import TempStaffing from "./TempStaffing.jpeg";
import BlueCollar from "./BlueCollar.jpeg";
import executive from "./executive.jpeg";
import about from "./about.jpeg";
import "./App.css";

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      nav: "home"
    };

    this.setNavElement = this.setNavElement.bind(this);
  }

  componentDidMount() {
    window.onscroll = function() {
      myFunction();
    };

    var navbar = document.getElementById("header");
    var sticky = navbar.clientHeight;

    function myFunction() {
      if (window.pageYOffset > sticky) {
        navbar.classList.add("sticky");
      } else {
        navbar.classList.remove("sticky");
      }
    }
  }

  setNavElement(nav) {
    this.setState({ nav });
  }

  render() {
    const navElement = this.state.nav;
    return (
      <div className="page">
        <div id="header" className="header">
          <div className="title">
            <img src={logo} width="200" height="150" />
          </div>
          <div className="navigation">
            <a
              className={
                navElement === "home"
                  ? "navigation-el selected"
                  : "navigation-el"
              }
              onClick={() => this.setNavElement("home")}
              href="#home"
            >
              Home
            </a>
            <a
              onClick={() => this.setNavElement("about")}
              className={
                navElement === "about"
                  ? "navigation-el selected"
                  : "navigation-el"
              }
              href="#about"
            >
              About Us
            </a>
            <a
              className={
                navElement === "contact"
                  ? "navigation-el selected"
                  : "navigation-el"
              }
              onClick={() => this.setNavElement("contact")}
              href="#contact"
            >
              Contact Us
            </a>
          </div>
        </div>
        <div className="content">
          <div className="section vision" id="home">
            <b>
              To be the global leader in the staffing industry and help solve
              the staffing challenges of organizations of any size.
            </b>
          </div>
          <div className="section home">
            <p>
              <div className="left-right">
                <img className="image" src={BlueCollar} />
                <div className="text">
                  <b>Blue Collar:</b> <br /> Blue collar hiring is very critical
                  as it forms the foundation of many large enterprises and
                  crucial for small business as well. We aim to help
                  organizations hire better in a shorter span of time.
                </div>
              </div>{" "}
            </p>{" "}
            <p>
              <div className="left-right">
                <div className="text">
                  <b>IT Staffing: </b> With technology playing an important role
                  in our daily lives it is important that organizations hire the
                  best talent. Tungsten focuses on helping organization hire the
                  talent that best suits the organization’s needs.{" "}
                </div>
                <img className="image" src={IT} />
              </div>
            </p>
            <p>
              <div className="left-right">
                <img className="image" src={TempStaffing} />
                <div className="text">
                  <b>Temp Staffing:</b> Business requirements are dynamic and
                  many organizations need talent for critical parts of their
                  projects.We understand the importance of this and offer
                  temporary manpower to ensure organization’s success.
                </div>
              </div>
            </p>
            <p>
              <div className="left-right">
                <div className="text">
                  <b>Executive Staffing:</b> <br />
                  Leadership hiring is very critical in any organization.
                  Tungsten understands this and works closely with the
                  organization to understand the required skill set to find the
                  right candidate. With a relentless approach, Tungsten ensures
                  that the best fit is identified through extensive research and
                  with its rich network of candidates.
                </div>
                <img className="image" src={executive} />
              </div>
            </p>
          </div>
          <div className="section about" id="about">
            <h2>About Us</h2>
            <p>
              Tungsten Solutions was established in the year 2014 with a vision
              to solve the staffing challenges of organizations across the
              globe. Headquartered in Coimbatore, Tungsten has clients from
              across the globe. Tungsten has redefined the recruitment
              experience of large enterprises to startups alike, Tungsten
              Solutions has changed the hiring mechanics, helping organizations
              hire across the recruitment spectrum.
            </p>
            <div className="about-image">
              <img className="about-img" src={about} />
            </div>
          </div>
          <div className="section contact" id="contact">
            <h2> Contact Us</h2>
            <p>
              If you have any questions, please write an email to
              <b>jobs@tungstensol.com</b> and we will answer you shortly.
            </p>
            <p>
              <b>Office Locations:</b> <br />
              USA: Detroit - Michigan <br />
              India : Coimbatore -Tamil Nadu
            </p>
          </div>
        </div>
        <div className="copywrite">© 2014 Tungsten Solutions</div>
      </div>
    );
  }
}

export default App;
